<template>
  <div id="app">
    <!--<h1 v-if="current_stone != 'all'">CURRENTLY VIEWING: {{ current_al }}</h1>-->
    <p id="bio" v-if="current_stone == 'all'">
        welcome to matthəw's website
        <br />
        <br />
        click on an animated icon above to view some things
    </p>
    <post></post>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
        stone2al: {'left': 'visual',
                    'top': 'textual',
                    'bottom': 'aural',
                    'right': 'interactive',
                    'center': 'personal',
                    'all': ''}
    }
  },
  methods: {
  },
  computed: {
      current_stone() {
          return this.$store.state.stone;
      },
      current_al() {
          return this.stone2al[this.current_stone];
      }
  },
  watch: {
      current_stone(new_stone, old_stone) {
      },
      current_al(new_al, old_al) {
      }
  }
}
</script>

<style>
body {
    font-family: "Comic Sans MS";
    font-size: 0.8em;
    background-color: white;
    color: #333;
}

app { 
    text-align: center;
}

#bio {
    text-align: center;
    font-size: 1.3em;
    color: #333;
}
</style>



