<template>
  <div class="all_posts">

      <div v-for="(al, index) in als">

        <div v-if="current_stone == index
                   && toggles[index][post['title']]"
             v-for="post in als[index]"
             v-bind:class="{ fullscreen: single_post,
                             'post': true,
                             index: true   }"
                    v-on:keyup.esc="modal_close" tabindex="0"
             >

        <!-- ALL POSTS -->
            <!-- NON LINK POSTS -->
            <div class='clickable_area'
                 v-if="post['type'] != 'link'"
                 v-on:click="toggle_post(index, post['title'])">

                <!-- GO BACK BUTTON to return to showing all posts -->
                <h3 v-if="single_post == true"
                    style="text-align: right; padding: 5px; color: #ccc;" >
                <em>X</em></h3>

                <img v-if="post['thumb'] != ''"
                    v-bind:src="img_src(post['thumb'])" />
                <h4>{{ post['title'] }}</h4>
                <p>{{ post['desc'] }}</p>
            </div>

            <!-- LINK POSTS -->
            <div class='clickable_area link'
                 v-if="post['type'] == 'link'">
                <a v-bind:href="post['link']">
                    <img v-if="post['thumb'] != ''"
                        v-bind:src="img_src(post['thumb'])" />
                    <h4><em>{{ post['title'] }}</em></h4>
                    <p>{{ post['desc'] }}</p>
                </a>
            </div>

        <!-- SINGLE POSTS -->
            <!-- THE FOLLOWING ONLY SHOWS UP WHEN A SINGLE POST IS VISIBLE -->
            <!-- {{ IMAGES }} -->
            <div v-if="single_post && post['type'] == 'images'"
                 v-for="image in post['images']"
                 class="del image">

                <img class="images" v-bind:src="img_src(image)"
                v-on:click.stop=modal_image(image) />

                <div id="myModal" class="modal"
                    v-on:click.stop="modal_close">

                    <span class="modal_close">X</span>

                    <img class="modal_img" id="img01" />
                </div> 
            </div>
            <!-- {{ MUSIC }} -->
            <div v-if="single_post && post['type'] == 'embeds'"
                 v-for="embed in post['embeds']"
                 class="del embed">

                <p v-html="embed"/></p>

            </div>
            <!-- {{ TEXTS }} -->
            <div v-show="single_post && post['type'] == 'text'"
                 v-for="(text, index) in post['texts']"
                 class="del texts">

                <span v-bind:id="'text_'+index"
                      class="textobj" 
                      v-html="return_html(text,index)"></span>

            </div>

      </div>

    </div>
  </div>
</template>

<script>
import Vue from 'vue'

import auraljson from './json/aural.json'
import visualjson from './json/visual.json'
import textualjson from './json/textual.json'
import interactivejson from './json/interactive.json'
import personaljson from './json/personal.json'

export default {
  name: 'app',
  data () {
    return {

        aural: auraljson,
        visual: visualjson,
        textual: textualjson,
        interactive: interactivejson,
        personal: personaljson,

        single_post: false,
        aural_toggles: {},
        visual_toggles: {},
        textual_toggles: {},
        interactive_toggles: {},
        personal_toggles: {},
    }
  },
  methods: {
      img_src(filename) {
          // if the link comes from the internet, we don't need to do anything
          // to the 'filename'
          if (filename.startsWith('http')) {
              return filename;
          } else {
          // if it is a local file, we need to prepend the source directory
              return "./src/assets/" + filename;
          }
      },

      modal_image(filename) {
              var modal = document.getElementById('myModal');
              var modalImg = document.getElementById('img01');

              modal.style.display = "flex";
          if (filename.startsWith('http')) {
              modalImg.src = filename;

          } else {
              modalImg.src = "./src/assets/" + filename;
          }
      },

      modal_close() {
          var modal = document.getElementById('myModal');
          if (modal != undefined && modal.style.display == "flex") {
              modal.style.display = 'none';
          }
      },

      return_html(filename, index) {
          $.get("./src/assets/texts/"+filename+".html", function(data) {
              $("#text_" + index).html(data);
          });
          return "";

      },

      toggle_post(dir, title) {
          if (this.single_post == false) {
              for (var k in this.jsons[dir]) {
                  var t = this.jsons[dir][k]['title'];
                  if (t != title) {
                      this.toggles[dir][t] = false;
                  }
              }
            // scroll the view down to 'hide' the banner and show the content
            $('html, body').animate({
                scrollTop: $(".all_posts").first().offset().top
                });
          } else {
              for (var k in this.jsons[dir]) {
                  var t = this.jsons[dir][k]['title'];
                  this.toggles[dir][t] = true;
              }
          }
          this.single_post = !this.single_post;
      },

      close_post(dir) {
          if (this.single_post == true) {
              for (var k in this.jsons[dir]) {
                  var t = this.jsons[dir][k]['title'];
                  this.toggles[dir][t] = true;
              }

          }
          this.single_post = false;
      },

      fill_toggles_array() {
          for(var k in auraljson) {
              Vue.set(this.aural_toggles, auraljson[k]['title'], true);
          }
          for(var k in visualjson) {
              Vue.set(this.visual_toggles, visualjson[k]['title'], true);
          }
          for(var k in textualjson) {
              Vue.set(this.textual_toggles, textualjson[k]['title'], true);
          }
          for(var k in interactivejson) {
              Vue.set(this.interactive_toggles, interactivejson[k]['title'], true);
          }
          for(var k in personaljson) {
              Vue.set(this.personal_toggles, personaljson[k]['title'], true);
          }

      }
  },
  beforeMount() {
      this.fill_toggles_array()
  },
  computed: {
      current_stone() {
          // reset things so that everything is displayed next time the same stone is selected
          this.fill_toggles_array();
          this.single_post = false;

          return this.$store.state.stone
      },
      als() {
          return {'left':this.visual, 'top':this.textual, 'bottom':this.aural, 'right':this.interactive, 'center':this.personal};
      },
      toggles() {
        return {'left': this.visual_toggles, 'top': this.textual_toggles, 'bottom':this.aural_toggles, 'right':this.interactive_toggles, 'center':this.personal_toggles}
      },
      jsons() {
        return {'left': this.visual, 'top': this.textual, 'bottom':this.aural, 'right':this.interactive, 'center':this.personal}
      }
  },
  watch: {
      current_stone(new_stone, old_stone) {
      },
      current_al(new_al, old_al) {
      }
  }
}

</script>

<style>
    body {
        background: #b8f4dd url('./assets/strokes.png');
    }
</style>

<style scoped>
.all_posts {
    margin: auto;
    width: 90%;
    padding: 20px;
    text-align: center;
} 

.post {
    width: 30%;
    padding: 10px;
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.25);
    display: inline-block;
    text-align: center;
    outline: none;
}
.fullscreen {
    width: 75%;
}
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 1em;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.9);
}
.del.image .modal_img {
    margin: auto;
    display: block;
    max-height: 70%;
    width: auto;

    animation-name: zoom;
    animation-duration: 0.6s;
}

@keyframes zoom {
    from {transform:scale(0)}
    to   {transform:scale(1)}
}

.modal_close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}
.modal_close:hover,
.modal_close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
}
@media only screen and (max-width: 700px){
    .modal_img {
        width: 100%;
    }
}

.del {
    display: inline-block;
    padding: 20px;
    margin: 1%;
}

.del.embed {
    width: 50%;

}
.del.image {
    max-width: 35%;

}

.del.image img {
    display: block;
    margin: auto;
    max-width: 100%;
}

.textobj {
    display: inline-block;
    background-color: #fefefe;
    padding: 2em;
    overflow: hidden;
    text-align: left;
    font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;

}

.del.texts {
    width: 75%;
    text-align: center;
}

.clickable_area {
    cursor: pointer;
    padding: 10px;
}

.clickable_area.link h4 {
    text-decoration: underline;
    color: #555;
}

.clickable_area.link a {
    text-decoration: none;
}

.clickable_area img {
    max-width: 160px;
}

.images {
    cursor: pointer;
    transition: 0.3s;
}
.images:hover {
    opacity: 0.7;
}
h4 {
    font-family: "Comic Sans MS";
    text-align: center;
}
</style>



