import Vue from 'vue'
import Velocity from 'velocity-animate'

import Banner from './Banner.vue'
import App from './App.vue'
import Post from './Post.vue'
import Vuex from 'vuex'
import store from './store'

Vue.use(Vuex);


new Vue({
  el: '#banner',
  store,
  render: h => h(Banner)
})

Vue.component(
    'post', {
    store,
    render: h => h(Post)
})

new Vue({
  el: '#app',
  store,
  render: h => h(App)

})




