<template>
    <div id='banner'>

      <div id="to_top"><em>to top</em></div>

        <!-- STONES -->
        <div id='stones'>
            <transition name="fade-left"
                        enter-active-class='stone-anim'
                        leave-active-class='stone-anim'>
                <div class='stone' id='stone_left'
                    v-show='current_stone == "all"
                        ||  current_stone == "left"'
                    @click='chooseStone("left")'>
                    <img src='./assets/left.gif'>
                </div>
            </transition>

            <transition name="fade-top"
                        enter-active-class='stone-anim'
                        leave-active-class='stone-anim'>
            <div class='stone' id='stone_top'
                    v-show='current_stone == "all"
                        ||  current_stone == "top"'
                    @click='chooseStone("top")'>
                <img src='./assets/top.gif'>
            </div>
            </transition>

            <transition name="fade-right"
                        enter-active-class='stone-anim'
                        leave-active-class='stone-anim'>
            <div class='stone' id='stone_right'
                    v-show='current_stone == "all"
                        ||  current_stone == "right"'
                    @click='chooseStone("right")'>
                <img src='./assets/right.gif'>
            </div>
            </transition>

            <transition name="fade-bottom"
                        enter-active-class='stone-anim'
                        leave-active-class='stone-anim'>
            <div class='stone' id='stone_bottom'
                    v-show='current_stone == "all"
                        ||  current_stone == "bottom"'
                    @click='chooseStone("bottom")'>
                <img src='./assets/bottom.gif'>
            </div>
            </transition>
            <transition name="fade-center"
                        enter-active-class='stone-anim'
                        leave-active-class='stone-anim'>
            <div class='stone' id='stone_center'
                    v-show='current_stone == "all"
                        ||  current_stone == "center"'
                    @click='chooseStone("center")'>
                <img src='./assets/center.gif'>
            </div>
            </transition>
        </div>
    </div>

</template>



<script>
export default {
    name: 'banner',
    data() {
        return {
            current_stone: 'all',
            bg_colors: {'top': '#fae9db',
                        'bottom': '#fadbdb',
                        'left': '#d8f5fd',
                        'right': '#e1dbfa',
                        'center': '#ebebeb'}
        }
    },

    methods: {
        chooseStone: function(loc) {
            var moves = { 'left': [134,0], 'top': [0,74],'right': [-134,0],
                          'bottom': [0,-74], 'center': [0, 0] }
            // reset the color (show all stones)
            if (this.current_stone == loc) {
                this.current_stone = 'all';

                // change bg color back to original
                $("body").css('background-color', '#b8f4dd');

//========================================
                this.$store.commit('set_all')

                // move current stone to back to its original place
                var stn = document.getElementById("stone_" + loc);
                Velocity(stn, {left: 0, top: 0}, {duration: 500});

            } else {
                // hides all but current stone
                this.current_stone = loc;

                // change bg color back to original
                $("body").css('background-color', this.bg_colors[loc]);

//========================================
                this.$store.commit('set_' + loc)

                // move current stone to center of grid after they disappear
                var stn = document.getElementById("stone_" + loc);
                Velocity(stn, {left: moves[loc][0], top: moves[loc][1]}, {duration: 500, delay: 200});

                // scroll the view down to 'hide' the banner and show the content
                //$('html, body').delay(500).animate({
                //    scrollTop: $(".all_posts").first().offset().top
                //    }, 300);
            }
        }
    }
}

</script>

<style scoped>
#banner {
    width: 90%;
    height: 300px;
    margin: auto;
}

#stones {
    display: grid;
    grid-template-columns: 128px 128px 128px;
    grid-template-rows: 64px 64px 64px;
    grid-gap: 10px;
    justify-content: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

#stone_left {
    grid-column: 1;
    grid-row: 2;
    position: relative;
}

#stone_top {
    grid-column: 2;
    grid-row: 1;
    position: relative;
}

#stone_right {
    grid-column: 3;
    grid-row: 2;
    position: relative;
}

#stone_bottom {
    grid-column: 2;
    grid-row: 3;
    position: relative;
    z-index: 2;
}

#stone_center {
    grid-column: 2;
    grid-row: 2;
    z-index: 1;
    position: relative;
}

.stone-anim, .fade-leave-active {
    transition: all .5s ease;
}

.fade-left-enter, .fade-left-leave-to {
    opacity: 0;
    transform: translateX(74px);
}
.fade-top-enter, .fade-top-leave-to {
    opacity: 0;
    transform: translateY(38px);
}
.fade-right-enter, .fade-right-leave-to {
    opacity: 0;
    transform: translateX(-74px);
}
.fade-bottom-enter, .fade-bottom-leave-to {
    opacity: 0;
    transform: translateY(-38px);
}
.fade-center-enter, .fade-center-leave-to {
    opacity: 0;
}

#to_top {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    padding: 1em;
    cursor: pointer;
}
</style>



