import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);


const store = new Vuex.Store({
    state: {
        stone: 'all'
    },
    getters: {
        which_stone: state => {
            return state.stone;
        }
    },
    mutations: {
        set_all: state => state.stone = 'all',
        set_top: state => state.stone = 'top',
        set_right: state => state.stone = 'right',
        set_bottom: state => state.stone = 'bottom',
        set_center: state => state.stone = 'center',
        set_left: state => state.stone = 'left'
    }
});

export default store;



